import React , { Component } from 'react';
import './transition.css';
import $ from 'jquery';
import Header from "../../components/header/header";
import Footer from "../../components/footer/footer";
import close_icon from '../../image/transition/close_icon.png'

// actions
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});

class Transition extends Component {
   constructor(props){
       super(props);
       this.state = {
            data_list:[1,1,1,1,1,1,1]
       };
   }
   next_page(){
       this.props.history.push('/transition_record');
   }
   // 
   is_active(){
        $('.choose_ele_content div').each(function(index,item){
            if($(item).hasClass('choose_active')){
                $(this).append('<img src="' + close_icon + '" alt="close_icon" />')
                $(this).siblings().find('img').remove();
            }
        })
    }
   componentDidMount(){
       let _this = this;
       // 
       this.props.dispatch(show_screen(1));
       let loding_alert = message.loading('...', 0)
       setTimeout(()=>{
           loding_alert();
           _this.props.dispatch(show_screen(0));
       }, 2500);
       _this.is_active();
       // 
        $('#record_icon').click(function(){
            $('#alert_content_record').css('display','block')
        })

        // 
        $('.alert_content_record').on('click','.choose_ele_content div',function(){
            if($(this).hasClass('choose_active')){
                $(this).removeClass('choose_active');
                $(this).find('img').remove();
            }else{
                $(this).addClass('choose_active').siblings().removeClass('choose_active')
            }
            _this.is_active()
        })

        // 
        $('#reset_btn').click(function(){
            $('.choose_ele_content div').each(function(index,item){
                if($(item).hasClass('choose_active')){
                    $(this).removeClass('choose_active')
                    $(this).find('img').remove();
                }
            })
        })
        
        // 
        $('#submit_btn').click(function(){
            $('#alert_content_record').css('display','none')
        })
   }
   render(){
       return (
           <div className="transition_page">
              <Header>
                    <div className='header_left'></div>
                    <div className='header_title'>
                        <div className="buy_icon title_btn_active"></div>
                        <div className="sale_icon"></div>
                    </div>
                    <div className='header_right'>
                        <img src={require('../../image/transition/record_icon.png')} className="record_icon" onClick={()=>this.next_page()} />
                        <img src={require('../../image/transition/screen.png')} className="record_icon" id="record_icon" />
                    </div>
              </Header>
              <div id='app___page_content' className="transition_page_content">
                    <div className="transition_page_content">
                        <div className="transition_page_content_header">
                            <div className="transition_page_content_header_active">ETH</div>
                        </div>
                        <p className="price_bar"><span className="font-size-14"></span><span className="font-size-16">2457.35</span></p>
                        <ul className="list_content">
                            {
                                this.state.data_list.map((item,index)=>{
                                    return <li className="list_ele flex-box " key={index}>
                                                <div className="around">
                                                    <div className="left_top"></div>
                                                    <div className="left_bottom"></div>
                                                    <div className="right_top"></div>
                                                    <div className="right_bottom"></div>
                                                </div>
                                                <div className="flex-box flex-ali-center flex-jus-center flex-col">
                                                    <img src={require('../../image/transition/default_icon.png')} className="list_ele_icon" alt="list_ele_icon"/>
                                                    <div className="flex-box flex-ali-center auth_status">
                                                        <img src={require('../../image/transition/auth.png')} alt="other_icon" className="other_icon"/>
                                                    </div>
                                                </div>
                                                <div className="list_ele_detail">
                                                    <div className="flex-box flex-ali-center flex-jus-bt">
                                                        <div className="flex-box flex-ali-center">
                                                            <span className="user_name">mmiaolian202</span>
                                                            <img src={require('../../image/transition/wx.png')} className="pay_icon" alt="pay_icon"/>
                                                            <img src={require('../../image/transition/zfb.png')} className="pay_icon" alt="pay_icon"/>
                                                            <img src={require('../../image/transition/yl.png')} className="pay_icon" alt="pay_icon"/>
                                                        </div>
                                                        <div className="price_content">2528CNY</div>
                                                    </div>
                                                    <p className="user_msg_bar deep_blue">
                                                        <span>:6334</span>
                                                        <span>:6334</span>
                                                        <span>:6334</span>
                                                    </p>
                                                    <div className="flex-box flex-ali-center flex-jus-bt buy_btn_content">
                                                        <span className="deep_blue">100-1785.46CNY</span>
                                                        <div className="buy_btn"></div>
                                                    </div>
                                                </div>
                                            </li>
                                })
                            }
                        </ul>
                    </div>
              </div>
                {/* <!--  --> */}
                <div className="alert_content_record" id="alert_content_record">
                    <div className="alert_content">
                        <div className="alert_list_content">
                            <div></div>
                            <div className="btn_group choose_ele_content">
                                <div></div>
                                <div></div>
                            </div>
                        </div>
                        <div className="alert_list_content">
                            <p></p>
                            <input type="text" className="input_money" placeholder=""/>
                        </div>
                        <div className="alert_list_content">
                            <p></p>
                            <div className="btn_group choose_ele_content">
                                <div></div>
                                <div></div>
                                <div></div>
                                <div></div>
                                <div>PayPal</div>
                                <div></div>
                                <div>SWIFT</div>
                                <div>PayNow</div>
                                <div>Paytm</div>
                                <div>QiWi</div>
                                <div>Interic<br/>e-transfer</div>
                            </div>
                        </div>
                        <div className="alert_list_content">
                            <div className="flex-box flex-ali-center flex-jus-bt">
                                <div></div>
                                <div className="flex-box flex-ali-center">
                                    <div className="no_wrap">(CNY)</div>
                                    <img src={require('../../image/transition/down_icon.png')} alt="down_icon" className="down_icon"/>
                                </div>
                            </div>
                            <div className="btn_group choose_ele_content">
                                <div>(CNY)</div>
                                <div>(USD)</div>
                                <div>(SGD)</div>
                            </div>
                        </div>
                        <div className="alert_list_content">
                            <div className="flex-box flex-ali-center flex-jus-bt">
                                <div></div>
                                <div className="flex-box flex-ali-center">
                                    <div className="no_wrap"></div>
                                    <img src={require('../../image/transition/down_icon.png')} alt="down_icon" className="down_icon"/>
                                </div>
                            </div>
                            <div className="btn_group choose_ele_content">
                                <div></div>
                                <div></div>
                                <div></div>
                            </div>
                        </div>
                        <div>
                            <div className="btn_group fun_btn_group flex-box">
                                <div className="reset_btn" id="reset_btn"></div>
                                <div className="fun_btn_group_active submit_btn" id="submit_btn"></div>
                            </div>
                        </div>
                    </div>
                </div>
              <Footer></Footer>
           </div>
       )
  }
}
// select
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Transition);
// export default Transition;


