import React , { Component } from 'react';
import './add_bank_card.css';

import Header from '../../components/header/header';

import { message, Button, Spin, Alert } from 'antd';

// 1. actions
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

class Add_bank_card extends Component {
   constructor(props){
       super(props);
       this.state = {
            post_value:{}
       };
       this.back_event = this.back_event.bind(this);
       this.input_event = this.input_event.bind(this);
       this.submit_event = this.submit_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   info = (msg) => {
        let _this = this
        message.config({
            top: '45%',
            duration: 1
        });
        message.info(msg).then(()=>{
            _this.props.dispatch(show_screen(0));
        });
    };
   input_event(el){
        this.state.post_value[el.target.dataset.type] = el.target.value;
   }
   submit_event(){
       let _this = this;
        this.props.dispatch(show_screen(1));
        let post_value = this.state.post_value;
        if(!post_value.user_name){
            return this.info('',2)
        }
        if(!post_value.id_card_num){
            return this.info('',2);
        }
        if(!post_value.bank_card_num){
            return this.info('',2);
        }
        if(!post_value.bank_name){
            return this.info('',2);
        }
        message.config({
            top: '45%'
        });
        let loding_alert = message.loading('...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
   }
   render(){
       return (
           <div className="add_bank_card_page">
              <Header>
                   <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" /> 
                   </div>
                   <div className='header_title'></div>
                   <div className='header_right'></div>
              </Header>
              <div id='app___page_content' className="add_bank_card_content">
                    <div className="add_bank_card_main">
                        <div className="list_ele">
                            <label htmlFor="user_name"></label>
                            <input type="text" id="user_name" data-type="user_name" onInput={this.input_event} placeholder=""/>
                        </div>
                        <div className="list_ele">
                            <label htmlFor="id_card_num"></label>
                            <input type="text" id="id_card_num" data-type="id_card_num" onInput={this.input_event} placeholder=""/>
                        </div>
                        <div className="list_ele">
                            <label htmlFor="bank_card_num"></label>
                            <input type="text" id="bank_card_num" data-type="bank_card_num" onInput={this.input_event} placeholder=""/>
                        </div>
                        <div className="list_ele">
                            <label htmlFor="bank_name"></label>
                            <input type="text" id="bank_name" data-type="bank_name" onInput={this.input_event} placeholder=""/>
                        </div>
                        <div className="list_ele">
                            <label htmlFor="bank_address"></label>
                            <input type="text" id="bank_address" data-type="bank_address" onInput={this.input_event} placeholder=""/>
                        </div>
                        <div className="submit_btn main-color-bg" onClick={this.submit_event}></div>
                    </div>
              </div>
           </div>
       )
  }
}
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Add_bank_card);
// export default Add_bank_card;


