import React , { Component } from 'react';
import './transition_record_detail.css';

import Header from '../../components/header/header';

// actions
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});


class Transition_record_detail extends Component {
   constructor(props){
       super(props);
       this.state = {

       };
       this.back_event = this.back_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   componentDidMount(){
        let _this = this;
        // 
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
   }
   render(){
       return (
           <div className="tranistion_record_detail">
              <Header>
                   <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" />
                   </div>
                   <div className='header_title'></div>
                   <div className='header_right'></div>
              </Header>
              <div id='app___page_content' className="tranistion_record_detail_content">
                    <div className="transition_record_page_content">
                        <h3 className="order_status"></h3>
                        <p className="flex-box flex-ali-center flex-jus-bt text-color-gray call_target">
                            <span></span>
                            <span></span>
                        </p>
                        <div className="list_title">
                            <div></div>
                            <div className="price">400.00</div>
                        </div>
                        <div className="list_content">
                            <div className="list_ele flex-box flex-ali-center flex-jus-bt">
                                <span></span>
                                <div className="flex-box flex-ali-center">
                                    <span>mmiaolian202</span>
                                    <img src={require('../../image/transition/auth.png')} alt="auth_icon" className="auth_icon" />
                                </div>
                            </div>
                            <div className="list_ele flex-box flex-ali-center flex-jus-bt">
                                <span></span>
                                <div className="flex-box flex-ali-center">
                                    <span>2567.99</span>
                                </div>
                            </div>
                            <div className="list_ele flex-box flex-ali-center flex-jus-bt">
                                <span></span>
                                <div className="flex-box flex-ali-center">
                                    <span>0.028279ETH</span>
                                </div>
                            </div>
                            <div className="list_ele flex-box flex-ali-center flex-jus-bt">
                                <span></span>
                                <div className="flex-box flex-ali-center">
                                    <span>2017-07-27&nbsp;15:17:17</span>
                                </div>
                            </div>
                            <div className="list_ele flex-box flex-ali-center flex-jus-bt">
                                <span></span>
                                <div className="flex-box flex-ali-center">
                                    <span>162537493726384</span>
                                </div>
                            </div>
                            <div className="list_ele flex-box flex-ali-center flex-jus-bt">
                                <span></span>
                                <div className="flex-box flex-ali-center">
                                    <span>558590</span>
                                </div>
                            </div>
                        </div>
                    </div>
              </div>
           </div>
       )
  }
}

// select
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Transition_record_detail);
// export default Transition_record_detail;


