import React , { Component } from 'react';
import './payment.css';

import Header from '../../components/header/header';

// actions
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});

class Payment extends Component {
   constructor(props){
       super(props);
       this.state = {

       };
       this.back_event = this.back_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   componentDidMount(){
        let _this = this;
        // 
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
   }
   render(){
       return (
           <div className="payment_page">
                <Header>
                    <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" />
                    </div>
                    <div className='header_title'></div>
                    <div className='header_right'></div>
                </Header>
                <div id='app___page_content' className="payment_page_content">
                    <div className="payment_main">
                        <div>
                            <p className="text-size-16"></p>
                            <p className="text-size-14">https://www.baidu.com</p>
                        </div>
                        <div>
                            <p className="text-size-16"></p>
                            <div className="input_content"><input type="text" placeholder="" /></div>
                        </div>
                        <div>
                            <p className="text-size-16"></p>
                            <div className="flex-box flex-ali-center flex-jus-bt input_content">
                                <input type="text" placeholder=""/><span className="all_btn"></span>
                            </div>
                        </div>
                        <p className="money_bar">ETH<span className="text-size-16 text-color-white">1789.67</span></p>
                        <div className="text-size-16 submit_btn main-color-bg"></div>
                        <p className="waring_bar"><span className="text-color-red margin-right-4">*</span></p>
                    </div>
                </div>
           </div>
       )
  }
}

// select
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Payment);
// export default Payment;


