import React , { Component } from 'react';
import './change_language.css';

import Header from '../../components/header/header';

// actions
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});


class Change_language extends Component {
   constructor(props){
       super(props);
       this.state = {

       };
       this.back_event = this.back_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   componentDidMount(){
        let _this = this;
        // 
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
   }
   render(){
       return (
           <div className="add_change_language_page">
              <Header>
                   <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" />
                   </div>
                   <div className='header_title'></div>
                   <div className='header_right'></div>
              </Header>
              <div id='app___page_content' className="add_change_language_content">
                <div className="add_change_language_main">
                    <div className="list_ele">
                        <label htmlFor="language_ch"></label>
                        <input type="radio" className="radio" defaultChecked id="language_ch" name="language"/>
                    </div>
                    <div className="list_ele">
                        <label htmlFor="language_en">&nbsp;&nbsp;&nbsp;&nbsp;</label>
                        <input type="radio" className="radio" id="language_en" name="language"/>
                    </div>
                    <div className="submit_btn main-color-bg"></div>
                </div>
              </div>
           </div>
       )
  }
}

// select
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Change_language);
// export default Change_language;


